<template>
    <div v-if="ready" style='height:87.5vh'>
        <div class="col-12 px0">
            <div id='top_page' class="upper_part">
                <div class="col-12 px0 flex">
                    <div class="col-3 px0">
                        <label>Select Department</label>
                        <div class="col-12 px0">
                            <select class="form-control select" v-model="dept" @change="fetch_data()">
                                 <option v-for="d in dept_list" :value="d.dept_id" v-if="d.in_dept==1">{{ d.full_dept }}</option>
                            </select>
                        </div>
                    </div>
                    <div class="col-1 px0">
                        <!--
                        <div>
                            <label>&nbsp;<label>
                        </div>
                        <div class="col-12">
                            <form method="post" action="src/Procurement/budget/backend/export.php">
                                <input type="hidden" name="export" value='n'/>
                                <button type="submit" class="form-control">Export</button>   
                            </form>
                        </div>
                        -->
                    </div>
                    <div class="col-2 px0">
                        <!--
                        <div>
                            <label>&nbsp;<label>
                        </div>
                        <div class="col-12">
                            <button type="submit" class="form-control" @click="approve_bp">Approve Selected Items</button>   
                        </div>
                        -->
                    </div>
                    <div class="col-3">
                    </div>                   
                    <div class="col-3 px0">
                        <div class="col-12 px0 flex">
                            <div class="col-4"></div>
                                <label>School Year</label>
                            </div>
                        <div class="col-12 flex px0">
                            <div class="col-4"></div>
                            <div class="col-4" style='padding-left:0'>
                                <input type="number" class="form-control" v-model="selected_st" @change="update_ed('from')"/>
                            </div>
                            <div class="col-4" style='padding-left:0'>
                                <input type="number" class="form-control" v-model="selected_ed" readonly/>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="col-12 flex px0" style='margin-top:1rem;'>
                    <div class="col-2 px0">
                        <div class="col-12 pl0">
                            <button type="submit" class="form-control" @click="approve_bp">Approve Selected Items</button>
                        </div>
                    </div>
                    <div class="col-1 px0">
                        <div class="pr0">
                            <form method="post" action="src/Procurement/budget/backend/export.php">
                                <input type="hidden" name="export" value='n'/>
                                <button type="submit" class="form-control">Export</button>   
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <div class="lower_part">
                <div class="col-12 px0">
                    <table class="table table-striped first-td-padding" style="font-size:0.875rem!important">
                        <thead>
                            <tr style="background-color:rgb(238,28,37)">
                                <th class="bp_th" style='vertical-align:middle!important;width:4rem!important'>
                                    <div class="col-12 px0">
                                        <input type="checkbox" class="form-control" :value="'all'" @click="check_all" v-model="all_checker"/>
                                    </div>
                                </th>
                                <th class="bp_th">ID</th>
                                <th class="bp_th">Description</th>
                                <th class="bp_th">Unit Cost</th>
                                <th class="bp_th">Quantity</th>
                                <th class="bp_th">Total Cost</th>
                                <th class="bp_th">Source of Fund</th>
                                <th class="bp_th">Status</th>
                                <th class="bp_th">Action</th>
                            </tr>
                        </thead>
                        <tbody v-for="l in data.length">
                            <tr class="asset_bar" style="background-color:rgb(238,28,37)" @click="change_child_dp(l-1)">
                                <td colspan="9">{{ data[l-1].ref_id }}. {{ data[l-1].asset }}</td>
                            </tr>
                            <tr v-show="show_child[l-1]!=false" v-if="data[l-1].item[0].tag!=null" v-for="n in data[l-1].item.length">
                                <td style='vertical-align:middle'>
                                    <div class="col-12 px0">
                                        <input type='checkbox' class="form-control" :value="data[l-1].item[n-1].tag" v-model="selected_tags" @click="print_val" :disabled="data[l-1].item[n-1].status[0]=='approve_principal'"/>
                                    </div>
                                </td>
                                <td>
                                    {{ data[l-1].item[n-1].tag }}
                                </td>
                                <td>
                                    {{ data[l-1].item[n-1].description[0] }}
                                </td>
                                <td>
                                    <div class="inner_item" v-for="d in data[l-1].item[n-1].cost_unit.length">
                                        {{ data[l-1].item[n-1].cost_unit[d-1] }}
                                    </div>
                                </td>
                                <td>
                                    <div class="inner_item" v-for="d in data[l-1].item[n-1].quantity.length">
                                        {{ data[l-1].item[n-1].quantity[d-1] }}
                                    </div>
                                </td>
                                <td>
                                    <div class="inner_item" v-for="d in data[l-1].item[n-1].cost_total.length">
                                        {{ data[l-1].item[n-1].cost_total[d-1] }}
                                    </div>
                                </td>
                                <td>
                                    <div class="inner_item" v-for="d in data[l-1].item[n-1].fundsrc.length">
                                        {{ data[l-1].item[n-1].fundsrc[d-1] }}
                                    </div>
                                </td>
                                <td :class="data[l-1].item[n-1].status[0]">{{ display_content(data[l-1].item[n-1].status[0]) }}</td>
                                <td>
                                    <div v-if="data[l-1].item[n-1].status[0]!='approve_principal'">
                                        <a class="action" @click="edit_form(data[l-1].item[n-1].tag)"><u>Edit Record</u></a>
                                    </div>
                                    <div v-if="data[l-1].item[n-1].status[0]=='approve_principal'">
                                        <a class="action" @click="edit_form(data[l-1].item[n-1].tag)"><u>View Details</u></a>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>  
        </div>
        <div class="corner_nav">
            <div class="col-12">
                <div class="corner_item" @click="scrollTop">
                    <i class="fa fa-2x fa-arrow-up" style='color:rgba(255,255,255,0.9)'></i>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return {
            all_checker:false,
            selected_tags:[],
            show_child:[],
            dept:'',
            dept_list:[],
            ready:false,
            selected_st:0,
            selected_ed:0,
            target_st:'',
            target_ed:'',
            data:[],
            user_check:'',
        }
    },
    async created(){
        await axios.get('Procurement/shared_backend/check_user_setup.php')
            .then(
                (response)=>{
                    this.user_check = response.data[0].signed;
                    var perm = response.data[0];
                    if(!(this.user_check=='set')){
                        this.$router.push('../user_setup');
                    }
                    if(perm.proc_level<3){
                        this.$alert("No permission").then(
                            ()=>{
                                this.$router.go(-1);
                            }
                        )
                    }
                }
            )
        await axios.get('Procurement/shared_backend/fetch_dept.php')
            .then(
                (response)=>{
                    this.dept_list = response.data;
                    var prev = document.getElementById('query').value;
                    if((prev!='')&&(prev!=null)&&(prev!=undefined)){
                        this.dept = prev;
                    } else {
                        this.dept = this.dept_list[0].dept_id;
                    }                        
                }
            )
        await axios.get('Procurement/shared_backend/default_period.php')
            .then(
                (response)=>{
                    var st = response.data.st;
                    var ed = response.data.ed;
                    var st_yr = st.split("-");
                    this.selected_st = parseInt(st_yr[0]);
                    this.target_st = parseInt(st_yr[0])+1;
                    var ed_yr = ed.split("-");
                    this.selected_ed = this.selected_st+1;
                    this.target_ed = this.target_st+1;
                }
            )
        await this.fetch_data();
        this.ready = true;
    },
    methods:{
        scrollTop(){
            document.getElementById('top_page').scrollIntoView();
        },
        edit_form(tag){
            document.getElementById('tmp_id').value=tag;
            document.getElementById('st_tmp').value=this.selected_st+'-08';
            document.getElementById('ed_tmp').value=this.selected_ed+'-07';
            this.$router.push('edit_bp_full');
        },
        print_val(){
            this.all_checker=false;           
        },
        approve_bp(){
            var formData = new FormData();
            formData.append('selected_id',this.selected_tags);
            formData.append('dept_id',this.dept);
            formData.append('st',this.selected_st);
            formData.append('ed',this.selected_ed);
            if(this.selected_tags.length>0){
                var msg = "Confirm approving budgets : "+this.selected_tags+' ?';
                this.$confirm(msg).then(
                    ()=>{
                        axios.post('Procurement/budget/backend/approve_bp.php',formData)
                        .then(
                            (response)=>{
                                if(response.data=='no_perm'){
                                    this.$alert('No Permission');
                                } else if(response.data=='OK'){
                                    this.$alert('Successfully Approved Budgets : '+this.selected_tags)
                                        .then(
                                            ()=>{
                                                this.fetch_data();
                                            }
                                        )
                                } else {
                                    this.$alert(response.data);
                                }
                            }
                        )
                    }
                )  
            } else {
                this.$alert("Please select at least one record");
            }
        },
        check_all(){
            var k = 0;
            console.log(this.all_checker);
            if(this.all_checker==false){
                var i = 0;
                for(i=0;i<this.data.length;i++){
                    var j = 0;
                    for(j=0;j<this.data[i].item.length;j++){
                        if((this.data[i].item[j].status[0]=='waiting')||(this.data[i].item[j].status[0]=='approve_head')){
                            this.selected_tags[k] = this.data[i].item[j].tag;
                            k++;
                        }
                    }
                }
            } else {
                this.selected_tags = [];
            }
            console.log(this.selected_tags);
            this.$forceUpdate();
        },
        display_content(status){
            var dp_content = '';
            switch(status){
                case 'approve_principal':
                    dp_content = 'Approved by Principal';
                break;
                case 'approve_head':
                    dp_content = 'Approved by Head';
                break;
                case 'waiting':
                    dp_content = 'Pending';
                break;
            }
            return dp_content;
        },
        change_child_dp(id){
            if(this.show_child[id]==false){
                this.show_child[id] = true;
            } else {
                this.show_child[id]=false;
            }
            this.$forceUpdate();
        },
        update_ed(mode){
            switch(mode){
                case "from":
                    this.selected_ed = parseInt(this.selected_st)+1;
                break;
                case "to":
                    this.target_ed = parseInt(this.target_st)+1;
                break;
            }           
            this.fetch_data();
        },
        async fetch_data(){
            document.getElementById('query').value = this.dept;
            this.selected_tags = [];
            var formData = new FormData();
            formData.append('dept',this.dept);
            formData.append('from_st',this.selected_st);
            formData.append('from_ed',this.selected_ed);
            formData.append('to_st',this.target_st);
            formData.append('to_ed',this.target_ed);
            await axios.post('Procurement/budget/backend/printlist_json.php',formData)
                .then(
                    (response)=>{
                        if(response.data!='empty'){
                            this.data = response.data;
                        } else {
                            this.data = [];
                        }
                    }
                )           
        },
    }    
}
</script>
<style scoped>
    .action{
        cursor:pointer;
    }
    .action:hover{
        color:#888;
    }
    .pl0{
        padding-left:0;
    }
    .pr0{
        padding-right:0;
    }
    .px0{
        padding-left:0;
        padding-right:0;
    }
    .flex{
        display:flex;
        flex-direction:row;
    }
    .lower_part{
        margin-top:1rem;
    }
    .asset_bar{
        color:#ffffff;
        background-color:rgb(238,28,37);
        border-bottom:2.5px solid #ffffff;
        cursor: pointer;
        transition: 0.3s;
        font-weight: 600;
    }
    .asset_bar:hover{
        background-color:rgb(156,10,14)!important;
    }
    .inner_item{
        height:2rem;
    }
    .inner_item:first-child{
        padding-top:11px;
    }
    td{
        border:1px solid white;
        vertical-align:middle;
    }
    .approve_principal{
        color: green;
    }
    .approve_head{
        color:rgb(139,184,232);
    }  
</style>


